<template>
    <div class="page page-form-validator">
        <h2></h2>

        <div class="attributes">
            <h2>API</h2>
            <table>
                <tr><th></th><th></th><th></th><th></th><th></th></tr>
                <tr>
                    <td>validator: {}</td>
                    <td>
                        <p>1,keystring </p>
                        <p>2,requiredboolean  </p>
                    </td>
                    <td>object</td>
                    <td>false</td>
                    <td>{}</td>
                </tr>
            </table>
        </div>

        <h2></h2>
        <ul class="listview listview-form custom">
            <!---->
            <li v-if="show1">
                <v-field v-model="value4" title="()" placeholder="2~5..." :limit="10" :validator="validator4"></v-field>
            </li>
            <li>
                <v-field v-model="value5" title="" placeholder="..." :validator="validator5"></v-field>
            </li>
            <li >
                <v-field v-model="value6" title="()" placeholder=": 111..." :validator="validator6"></v-field>
            </li>
            <!---->
            <li v-if="show2">
                <v-field v-model="value7" title="" placeholder=": 010-88889999..." :validator="validator7"></v-field>
            </li>
            <li>
                <v-field v-model="value8" title="" placeholder="..." :validator="validator8"></v-field>
            </li>
            <li  @click="open('picker1')">
                <v-field is-link title="()" placeholder="" :validator="validatorStudentIdengy" v-model="studentIdengyType"></v-field>
            </li>
            <li>
                <v-field v-model="studentIdenty" title="()" placeholder=""  :validator="validatorStudentNumber"></v-field>
            </li>
            <li>
                <v-field v-model="value11" title="()" placeholder="..." :validator="validator11"></v-field>
            </li>
            <li>
                <v-field v-model="value12" title="" placeholder="..." :validator="validator12"></v-field>
            </li>
            <!---->
            <li v-if="show3">
                <v-field v-model="value13" title="" placeholder="520..." :validator="validator13"></v-field>
            </li>
        </ul>
        <div style="display:flex;flex-flow:row nowrap;justify-content: space-around;align-items: center">
            <v-button style="margin:10px;" @click="show1 = !show1"></v-button>
            <v-button style="margin:10px;" @click="show2 = !show2"></v-button>
            <v-button style="margin:10px;" @click="show3 = !show3"></v-button>
        </div>
        <v-row :gutter="30" v-sticky.bottom="0">
            <v-button size="full" styles="rectangle" @click="onSubmit">submit</v-button>
        </v-row>

        <div class="blank"></div>
        <v-popup v-model="popupStudent" ref='picker1' class="v-popup-4">
            <div class="picker-button">
                <span class="picker-cancel" @click="pickerClose('picker1')"></span>
                <span class="picker-confirm" @click="confirm('picker1')"></span>
            </div>
        <v-picker ref='vpicker1' :slots="cardSlots"  valueKey="values" @change="onPicker1Change" :visible-item-count="5" :show-toolbar="true"></v-picker>
        </v-popup>
    </div>
</template>

<script>
import vField from '../vendor/v-field';
import vRow from '../vendor/v-row.vue';
import vCell from '../vendor/v-cell.vue';
import vButton from '../vendor/v-button.vue';
import vPicker from '../vendor/v-picker';
import vPopup from '../vendor/v-popup';
import Vue from 'vue';
import sticky from '../vendor/v-sticky.js';
Vue.use(sticky);

export default {
    components: { vRow, vCell, vField, vButton, vPicker, vPopup },
    data () {
        return {
            show1: true,
            show2: true,
            show3: true,
            validator: this.$validation,
            value4: '',
            validator4: {
                key: 'validator4',
                required: true,
                minLength: {
                    rule: 2,
                    message: '2~'
                },
                maxLength: {
                    rule: 5,
                    message: '5~'
                }
            },
            value5: '',
            validator5: {
                key: 'validator5',
                mail: {
                    rule: 1,
                    message: '~'
                }
            },
            value6: '',
            validator6: {
                key: 'validator6',
                required: {
                    message: ''
                },
                mobile: {
                    rule: 1,
                    message: '~'
                }
            },
            value7: '',
            validator7: {
                key: 'validator7',
                tel: {
                    rule: 1,
                    message: '~'
                }
            },
            value8: '',
            validator8: {
                key: 'validator8',
                card: {
                    rule: 1,
                    message: '~'
                }
            },
            value11: '',
            validator11: {
                key: 'validator11',
                required: {
                    message: ''
                },
                numbers: {
                    rule: 1,
                    message: '~'
                }
            },
            value12: '',
            validator12: {
                key: 'validator12',
                text: {
                    rule: 1,
                    message: '~'
                }
            },
            value13: '',
            validator13: {
                key: 'validator13',
                username: {
                    rule: 1,
                    message: '~'
                }
            },
            //                
            valstudentIdengyTypeId: '',
            studentIdengyTypeId: '',
            studentIdengyType: '',
            validatorStudentIdengy: {
                key: 'validator9',
                required: {
                    rule: true,
                    message: ''
                }
            },
            studentIdenty: '',
            validatorStudentNumber: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                }
            },
            //                
            validatorCard: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                card: {
                    message: ''
                }
            },
            //                
            validatorPassport: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                passport: {
                    message: ''
                }
            },
            //                
            validatorSorderIdenty: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                sorderIdenty: {
                    message: ''
                }
            },
            //                
            validatorSocialSecurity: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                socialSecurityCard: {
                    message: ''
                }
            },
            //                
            validatorHongKongMacauPasser: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                hongKongMacauPasser: {
                    message: ''
                }
            },
            //                
            validatorTaiwanPasser: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                taiwanPasser: {
                    message: ''
                }
            },
            //                
            validatorHouseHoldRegister: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                taiwanPasser: {
                    message: ''
                }
            },
            //                
            validatorInterimId: {
                key: 'validator10',
                required: {
                    rule: true,
                    message: ''
                },
                taiwanPasser: {
                    message: ''
                }
            },
            popupStudent: false,
            cardSlots: [
                {
                    flex: 1,
                    values: [
                        {key: '1', values: ''},
                        {key: '2', values: ''},
                        {key: '3', values: ''},
                        {key: '4', values: ''},
                        {key: '5', values: ''},
                        {key: '6', values: ''},
                        {key: '7', values: ''},
                        {key: '8', values: ''},
                        {key: '9', values: ''}
                    ]
                }
            ]
        };
    },
    mounted () {
        this.$logger.log('form-field mounted... ');
        this.init();
    },
    watch: {
        studentIdengyTypeId () {
            if (this.studentIdengyTypeId === '0') {
                this.studentIdengyType = '';
            } else {
                this.studentIdengyType = this.cardSlots[0].values[this.studentIdengyTypeId - 1].values;
            }
        }
    },

    methods: {
        init () {
        },
        onSubmit () {
            if (this.validate()) {
                this.$toast('');
            }
        },
        validate () { //  Author by Dio Zhu on 2017.8.16
            console.error(this.$validation, 'validation');
            /** message */
            for (let key in this.$validation) {
                if (this.$validation.hasOwnProperty(key)) {
                    for (let k in this.$validation[key]) {
                        if (this.$validation[key].hasOwnProperty(k)) {
                            this.$toast(this.$validation[key][k]);
                            return false;
                        }
                    }
                }
            }
            return true;
        },
        // id
        findValidate (id) {
            if (id === '0') {
                return this.validatorCard;
            };
            if (id === '1') {
                return this.validatorCard;
            };
            if (id === '2') {
                return this.validatorPassport;
            };
            if (id === '3') {
                return this.validatorSorderIdenty;
            };
            if (id === '4') {
                return this.validatorSocialSecurity;
            };
            if (id === '5') {
                return this.validatorHongKongMacauPasser;
            };
            if (id === '6') {
                return this.validatorTaiwanPasser;
            };
            if (id === '7') {
                return this.validatorHouseHoldRegister;
            };
            if (id === '8') {
                return this.validatorInterimId;
            };
        },
        open (picker) {
            let slotValue = {
                key: this.studentIdengyTypeId || '1',
                values: this.studentIdengyType || ''
            };
            if (picker === 'picker1') this.$refs['v' + picker].setSlotValue(0, slotValue);
            this.$refs[picker].open();
        },
        onPicker1Change (picker, values) {
            if (values[0] > values[1]) {
                picker.setSlotValue(1, values[0]);
            }
            this.valstudentIdengyTypeId = values[0].key;
        },
        confirm () {
            this.studentIdengyTypeId = this.valstudentIdengyTypeId;
            //       
            if (this.studentIdengyTypeId === '0') {
                this.studentIdengyTypeId = '1';
            }
            this.validatorStudentNumber = this.findValidate(this.studentIdengyTypeId);
            let that = this;
            console.error(that.validatorStudentNumber, '');
            console.error(that.$validation, 'validation');
            this.pickerClose('picker1');
        },
        pickerClose (picker) {
            this.$refs[picker].close();
        }
    }
};
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "../scss/variables";
    @import "../scss/mixins";

    .page-form-validator {

        .v-cell__value {
            margin-left: 0;
        }

        .form-buttons {
            text-align: right;
            padding: pxTorem(10) pxTorem(22);
        }

        .custom {

            .placeholder-blue {

                input,
                textarea {
                    &::-webkit-input-placeholder {/* WebKit browsers */
                        color: #007aff;
                    }
                }
            }

            .v-field__core {

                &.error {
                    border-left: #fa5a5a pxTorem(3px) solid;
                    padding-left: pxTorem(7px);
                }
            }
        }

        .blank {
            width: 100%;
            height: pxTorem(100px);
        }
    }
</style>
