<template>
    <div v-show="isEnabled"
         class="v-scroll"
    >
        <!--v-scroll="getList"-->
        <!--scroll-disabled="isLoading"-->
        <!--scroll-distance="scrollDistance"-->
        <!--scroll-enabled="isEnabled"-->
        <!--scroll-tombstone="tombstone"-->
        <!--scroll-scrolling-func="scrollingFunc"-->
        <!--v-scroll-position-->
        <div
            :id="'combScroll_'+_uid"
            ref="container"
            class="v-scroll-container"
        >
            <!--:style="{ 'transform': 'translate3d(0, ' + translate + 'px, 0)' }"-->
            <!--  -->
            <!--<div ref="content" class="v-scroll-content">-->
            <transition-group name="modal" ref="content" class="v-scroll-content" tag="div">
                <slot></slot>
                <!-- <slot name="footer" :class="[]"></slot> -->
            <!--</div>-->
            </transition-group>
            <!-- loading &&  -->
            <!--<div v-if="typeof endFunc == 'function' || (!refreshTag && (isLoading || (isShowText && scrollEndTxt && currentValue.length > 0)))" class="v-scroll-bottom">-->
            <div v-show="typeof endFunc == 'function' || (!refreshTag && (hasMore || (isShowText && scrollEndTxt && currentValue.length > 0)))"
                 class="v-scroll-bottom"
                 v-scrollor="getList"
            >
                <transition name="fade">
                    <!--<v-spinner v-show="isLoading" color="#BBBAC2" type="triple-bounce"></v-spinner>-->
                    <v-spinner v-show="hasMore" color="#BBBAC2" type="triple-bounce"></v-spinner>
                </transition>
                <transition name="fade">
                    <p class="v-scroll-bottom-txt" v-show="!isLoading && isShowText && scrollEndTxt && currentValue.length > 0">
                        <slot name="endText">&nbsp;&nbsp;</slot>
                    </p>
                </transition>
            </div>
            <!--  -->
            <div v-if="!hasData && currentValue.length <= 0" class="v-scroll-empty">
                <slot class="v-scroll-empty" name="empty"></slot>
            </div>
        </div>
        <!--  -->
        <transition name="fade">
            <div v-show="toper && goTopTag" @click="goTop" class="v-scroll-top icon icon-top"></div>
        </transition>
    </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue';
import ScrollerJS from './v-scroller.js';
import vSpinner from '../vendor/v-spinner/';
import CONFIG from '../config';
import * as api from '../js/core/api.js'; //eslint-disable-line
import bus from '../vendor/eventbus.js';
import * as utils from '../js/utils/utils.js';
// import * as dom from '../js/utils/dom.js';
Vue.use(ScrollerJS);

/**
 * scroll 
 *              -- Author by Dio Zhu. on 2017.2.11
 * scroll'END'
 *              -- Mod by Dio Zhu. on 2017.3.13
 * limit + offsetapi
 * Promise.resolvereturn Promise.resolve(dat);
 * scrollgetList
 *              -- Mod by Dio Zhu. on 2017.3.13
 * funcTypepage
 * section nodejs
 *      offset: 0, limit: 10
 *      offset: 10, limit: 20
 * page  php
 *      page: 1, pageNum: 10
 *      page: 2, pageNum: 10
 * limit  
 *      offset: 0, limit: 10
 *      offset:10, limit: 10
 * enabled
 * 
 *              -- Mod by Dio Zhu. on 2017.3.17
 * time 
 *      oTime-1pageSize10
 *      oTime: pageSize10
 *              -- xufeng on 2017.4.17
 */
export default {
    name: 'v-scroller',

    props: {
        from: String,
        id: String,
        value: {
            type: Array,
            default: () => []
        },
        func: Function,             // 
        endFunc: Function,          // 
        scrollingFunc: Function,    // 
        funcType: {             // sectionnodejs(0~10, 10~20)pagephptime
            type: String,
            default: 'page'
        },
        enabled: {             // 
            type: Boolean,
            default: true
        },
        pullAbled: {            // 
            type: Boolean,
            default: false
        },
        toper: {
            type: Boolean,      // 
            default: false
        },
        scrollDistance: { // 
            type: Number,
            default: 100
        },
        tombstone: {
            type: Boolean,      // 
            default: false
        },
        hasDataParent: {
            type: Boolean,
            default: true
        },
        isGoInit: {
            type: Boolean,
            default: true
        },
        scrollEndFlowTag: {     // falsetrue
            type: Boolean,
            default: false
        },
        endFuncEnable: {        // endFunc
            type: Boolean,
            default: false
        }
        // loading: {
        //     type: Boolean,
        //     default: false
        // },
        // isLoadMore: Boolean, // 
        // dataExist: Boolean,  // 
        // classes: String      // class
    },

    components: {
        'v-spinner': vSpinner
    },

    data () {
        return {
            pageHeight: 0,
            uid: this._uid,
            currentValue: this.value,

            //     scrollTop: 0,               // 
            goTopTag: false,            // 

            oTime: -1,                  // ()
            pageSize: CONFIG.LIMIT,     // ()
            offset: 0,                  // (nodejs)
            limit: CONFIG.LIMIT,        // (nodejs)
            page: 1,                    // (php)
            pageNum: CONFIG.LIMIT,      // (php)
            hasMore: true,              // , " -  - "
            hasMoreTotal: true,         // endFunc
            isLoading: false,           // 
            //     listCount: 0,               // 
            scrollEndTxt: false,        // 
            refreshTag: false,          // 
            hasData: true,
            isEnabled: this.enabled,    // 
            //     translate: 0,               // 
            //     refreshHeight: 0,           // refreshdom
            //     refreshTranslate: 0,        // refreshdom
            scrollTarget: null          // dom
        };
    },

    computed: {
        isShowText () {
            //     this.$logger.log('--------> ', this.dataExist, this.isLoadMore);
            return !!(this.hasData && !this.hasMore);
            //     return !!(this.hasData && !this.hasMore && document.body.scrollTop > 100);
        }
    },

    watch: {
        value (val) {
            this.currentValue = val;
        },
        currentValue (val) {
            this.$emit('input', val);
        },
        enabled (val) {
            this.$logger.log('v-scroll.watch.enabled! ');
            //     this.isEnabled = !this._inactive && val;
            this.isEnabled = val;
            //     if (val) this.$router.scrollTarget = this.scrollTarget; // 
            //     if (val === true && this.listCount <= 0) {
            if (val === true && this.value.length <= 0) {
                //         this.init();
                this.refreshList();
            }
        }
    },

    created () {
        this.$logger.log(`[v-scrollor].${this._uid}.created...`);
        bus.$on('v-scroll.refreshList', this.refreshList); // 
        bus.$on('v-scroll.getList', this.getList); // 
        // this.init();
        if (!this.$route.meta || !this.$route.meta.keepAlive) this.init(); // keepAliveinit Author by Dio Zhu. on 2017.12.2
    },
    mounted () {
        this.pageHeight = window.screen.height;
        this.$logger.log(`[v-scrollor].${this._uid}.mounted...`, this.$route.name, this.$parent.$route.name);
    },

    activated () {
        this.$logger.log(`*[v-scrollor].${this._uid}.activated...`, this._inactive, this.$router.direct());
        this.isEnabled = this.enabled;
        // this.init(); // keep-alive
        if (this.$router.direct()) { // in
            this.init(); // keep-alive
        } else { // back
            // do nothing ...
        }

        // dom.addClass(window.document.documentElement, 'overflow'); // bodyoverflow
    },
    deactivated () {
        this.$logger.log(`*[v-scrollor].${this._uid}.deactivated...`, this._inactive);
        this.isEnabled = false;

        // dom.removeClass(window.document.documentElement, 'overflow'); // overflow
    },
    beforeDestroy () {
        this.$logger.log(`*[v-scrollor].${this._uid}.beforeDestroy...`);
    },

    methods: {
        reset ({resetData = true, goTop = true} = {}) {
            // 
            this.oTime = -1;
            this.pageSize = CONFIG.LIMIT;
            this.offset = 0;
            this.limit = CONFIG.LIMIT;
            this.page = 1;
            this.pageNum = CONFIG.LIMIT;
            this.hasMore = true;
            this.hasData = true;
            this.scrollEndTxt = false;
            // this.endFuncEnable = false; // refresh

            if (resetData) this.currentValue = [];
            this.scrollTarget = window; // window
            if (goTop && this.scrollTarget) this.scrollTarget.scrollTop = 0;
            this.hasMoreTotal = true;

            if (!this.fetcher) this.fetcher = api.getFetcher(this.func, this.funcType); // fetcher
            this.fetcher.init();
        },

        init () {
            this.$logger.log(`[v-scrollor].${this._uid}.init...`);
            this.reset(); // 

            if (!this.isGoInit) return false; // 

            // 
            // if (!this.tombstone) this.getList();

            const e = utils.requestAnimationFrame,
                t = () => {
                    this.updateItems();
                    e(t);
                };
            if (this.tombstone) t();
            // this.$logger.log(`[v-scrollor].${this._uid}.init...`, this.$refs.content.$el);
        },

        updateItems () {
            // this.$logger.log(`[v-scrollor].${this._uid}.updateItems: `);
            let e = this.$refs.container,
                t = e.offsetTop,
                i = window.scrollY,
                n = i + window.innerHeight,
                o = t;
            [].forEach.call(this.currentValue, (val, idx) => {
                // this.$logger.log(`[v-scrollor].${this._uid}.updateItems.forEach: ${idx}`, o > i - 500, o < n + 500);
                // this.$logger.log(`[v-scrollor].${this._uid}.updateItems.forEach: ${idx}`, o, i, n);
                // o > i - 500 && o < n + 500 && (val.shown = !0);
                o > i - 500 && o < n + 500 && this.$set(this.currentValue[idx], 'shown', !0);
                o += val.height || 0;
            });
            this.$nextTick(this.updateItemsHeight);
        },
        updateItemsHeight () {
            [].forEach.call(this.currentValue, (val, idx) => {
                let i = this.$refs.content.$el.children[idx];
                // this.$logger.log(`[v-scrollor].${this._uid}.updateItemsHeight.forEach: ${idx}`, i.offsetHeight);
                // let i = document.querySelector(`.item-${idx}`);
                i && (val.height = i.offsetHeight);
            });
        },
        goTop () {
            let target = this.scrollTarget === window ? document.body : this.scrollTarget;
            this.$logger.log(`[v-scrollor].${this._uid}.getList.after: `, target.scrollHeight, target.offsetHeight);
            if (target.scrollTop > 0) {
                target.scrollTop -= 1000;
                setTimeout(this.goTop, 30);
            }
        },

        /**
         * 
         */
        getList () {
            this.$logger.log(`[v-scrollor].${this._uid}.getList.befor: !!!`);
            if (!this.isEnabled) return Promise.reject({});  // 
            if (this.isLoading) return Promise.reject({});  // 
            // 
            // if (!this.scrollEndTxt && this.scrollTarget) this.scrollEndTxt = document.body.scrollHeight > document.body.offsetHeight;
            // let target = this.scrollTarget === window ? document.body : this.scrollTarget;
            // if (!this.scrollEndTxt && target) this.scrollEndTxt = this.scrollEndFlowTag ? true : target.scrollHeight > (target.offsetHeight * 5 / 4);

            //     if (typeof this.func !== 'function' || !this.hasMore) return;  // 
            /**
             * endFunc
             * v1.01''
             *              -- Author by Dio Zhu. on 2017.12.5
             */
            if (typeof this.func !== 'function') return Promise.reject({});  // 
            if (typeof this.endFunc === 'function' && !this.hasMore && this.hasMoreTotal && this.endFuncEnable) { // 
                try {
                    if (this.isLoading) return Promise.reject({});
                    if (this.isEnding) return Promise.reject({});
                    this.isEnding = true;
                    this.isLoading = true;
                    return this.endFunc().then(res => {
                        //                 this.$logger.log(`[v-scrollor].${this._uid}.endFunc.after: `, res);
                        if (res) {
                            this.$logger.log(`[v-scrollor].${this._uid}.endFunc.after: `, res);
                            this.reset({resetData: false, goTop: false}); // 
                        //                     this.getList();
                        } else {
                            this.hasMoreTotal = false;
                        }
                        this.isEnding = false;
                        this.isLoading = false;
                        return Promise.resolve({});
                    });
                } catch (e) {
                    this.$logger.error(`[v-scrollor].${this._uid}.endFunc.after.error: ${e}`);
                    return Promise.reject({});
                }
            }
            if (!this.hasMore) return Promise.reject({});  // 
            /**
             * end mod. on 2017.12.5
             */

            /**
             * api
             * apifetcher
             *              -- Author by Dio Zhu. on 2017.12.11
             */
            try {
                this.isLoading = true;
                if (!this.fetcher) this.fetcher = api.getFetcher(this.func, this.funcType); // fetcher
                return this.fetcher.fetch().then(res => {
                    this.$logger.log(`[v-scrollor].${this._uid}.getList.api.after.success: `, res);
                    if (res && res.length) { // 
                        this.hasData = true;
                        this.currentValue = this.currentValue.concat(res); // 
                    } else if (!this.currentValue || !this.currentValue.length) {
                        this.hasData = false;
                    }
                    /** ,  */
                    if (!res || res.length < this.fetcher.limit) this.hasMore = false; // 
                }).then(res => {
                    this.$logger.log(`[v-scrollor].${this._uid}.getList.after: `, this.scrollTarget);
                    if (this.scrollTarget === window) {  // . mod by Dio Zhu. on 2017.12.12
                        this.$logger.log(`[v-scrollor].${this._uid}.getList.after: `, window.innerHeight, document.body.offsetHeight);
                        this.$nextTick(() => { this.scrollEndTxt = this.scrollEndFlowTag ? true : document.body.offsetHeight > (window.innerHeight * 5 / 4); });
                    } else if (this.scrollTarget) {
                        this.$logger.log(`[v-scrollor].${this._uid}.getList.after: `, this.scrollTarget.scrollHeight, this.scrollTarget.offsetHeight);
                        this.$nextTick(() => { this.scrollEndTxt = this.scrollEndFlowTag ? true : this.scrollTarget.scrollHeight > (this.scrollTarget.offsetHeight * 5 / 4); });
                    }
                    this.isLoading = false;
                    return Promise.resolve({});
                });
            } catch (e) {
                this.$logger.error(`[v-scrollor].${this._uid}.getList.after.error: ${e}`);
                this.isLoading = false;
                return Promise.reject({});
            }
        },

        refreshList () {
            //  || this._isDestroyedkeepalive
            if (!this.isEnabled || this._isDestroyed) return;  // 
            this.$logger.log(`!!!v-scroll.${this._uid}.refreshList...`);
            if (typeof this.func !== 'function') return;  // 
            this.refreshTag = true;

            /**
             * api
             * apifetcher
             *              -- Author by Dio Zhu. on 2017.12.11
             */
            try {
                this.isLoading = true;
                if (!this.fetcher) this.fetcher = api.getFetcher(this.func, this.funcType); // fetcher
                this.reset({resetData: false, goTop: true}); // ~ Author by Dio Zhu. on 2017.4.12
                return this.fetcher.fetch().then(res => {
                    this.$logger.log(`[v-scrollor].${this._uid}.refreshList.api.after.success: `, res);
                    if (res && res.length) { // 
                        this.hasData = true;
                        this.currentValue = res; // 
                    } else if (!this.currentValue || !this.currentValue.length) {
                        this.hasData = false;
                        this.currentValue = []; // 
                    }
                    /** ,  */
                    if (!res || res.length < this.fetcher.limit) this.hasMore = false; // 
                }).then(res => {
                    this.$logger.log(`[v-scrollor].${this._uid}.refreshList.after: `, this.scrollTarget);
                    if (this.scrollTarget === window) {  // . mod by Dio Zhu. on 2017.12.12
                        this.$logger.log(`[v-scrollor].${this._uid}.refreshList.after: `, window.innerHeight, document.body.offsetHeight);
                        this.$nextTick(() => { this.scrollEndTxt = this.scrollEndFlowTag ? true : document.body.offsetHeight > (window.innerHeight * 5 / 4); });
                    } else if (this.scrollTarget) {
                        this.$logger.log(`[v-scrollor].${this._uid}.refreshList.after: `, this.scrollTarget.scrollHeight, this.scrollTarget.offsetHeight);
                        this.$nextTick(() => { this.scrollEndTxt = this.scrollEndFlowTag ? true : this.scrollTarget.scrollHeight > (this.scrollTarget.offsetHeight * 5 / 4); });
                    }
                    this.isLoading = false;
                    this.refreshTag = false;
                    // //             }).then(res => { // endFuncgetList Author by Dio Zhu. on 2017.12.7
                    // //                 if (typeof this.endFunc === 'function' &&
                    // //                     !this.hasMore && this.hasMoreTotal && this.endFuncEnable &&
                    // //                     this.currentValue && this.currentValue.length <= 1
                    // //                 ) { // 
                    // //                     this.$logger.log(`[v-scrollor].${this._uid}.refreshList.after.endFunc: `, typeof this.endFunc, this.endFuncEnable);
                    // //                     try {
                    // //                         if (this.isLoading) return;
                    // //                         if (this.isEnding) return;
                    // //                         this.isEnding = true;
                    // //                         this.isLoading = true;
                    // //                         this.endFunc().then(res => {
                    // //  //                 this.$logger.log(`[v-scrollor].${this._uid}.endFunc.after: `, res);
                    // //                             if (res) {
                    // //                                 this.$logger.log(`[v-scrollor].${this._uid}.refreshList.endFunc.after: `, res);
                    // //                                 this.reset({resetData: false, goTop: false}); // 
                    // //                                 this.getList();
                    // //                             } else {
                    // //                                 this.hasMoreTotal = false;
                    // //                             }
                    // //                             this.isEnding = false;
                    // //                             this.isLoading = false;
                    // //                         });
                    // //                     } catch (e) {
                    // //                         this.$logger.error(`[v-scrollor].${this._uid}.refreshList.endFunc.after.error: ${e}`);
                    // //                     }
                    // //                 }
                });
            } catch (e) {
                this.$logger.error(`[v-scrollor].${this._uid}.refreshList.after.error: ${e}`);
                this.isLoading = false;
                this.refreshTag = false;
            }
        }
    }
};
</script>
<style rel="stylesheet/scss" lang="scss">
